@use "base/variables" as v;
@use "base/mixins" as m;

.hero {
    padding-top: 7rem;
    @include m.desktop {
        padding-top: 10rem;
    }
}

.about {
    .about-meta {
        width: 100%;
    }

    .about-meta,
    .about-title,
    .about-description {
        text-align: center;
    }

    .about-title {
        font-size: calc(1.5rem + 0.5vw);
    }

    .profile,
    .contact-info {
        justify-content: center;
    }

    .about-img {
        display: none;
    }

    @include m.phone {

        .about-meta,
        .about-title,
        .about-description {
            text-align: left;
        }

        .profile,
        .contact-info {
            justify-content: start;
        }
    }

    @include m.desktopXL {
        .about-img {
            display: block;
        }
    }
}

.features {

    .tab-content {
        h3 {
            text-align: center;
        }
    }
    h3 {
        font-size: calc(1.1rem + 0.6vw) !important;

        &:after {
            width: 90% !important;
            right: 0;
            margin: auto;
        }
    }

    span {
        font-size: calc(0.8rem + 0.3vw) !important;
    }

    @include m.phone {
        .tab-content {
            h3 {
                text-align: left;
            }
        }
        h3 {
            &:after {
                width: 75% !important;
                right: unset;
            }
        }
    }
}

.faq-9 {
    h3 {
        font-size: calc(0.8rem + 0.3vw) !important;
    }
    .faq-title, .faq-description {
        text-align: center;
    }
    @include m.phone {
        .faq-title, .faq-description {
            text-align: left;
        }
    }
}

.footer {
    .footer-about {
        display: flex;
        flex-direction: column;
        align-items: center;
        .logo {
            margin: auto;
            margin-top: 0;
            margin-bottom: 0;
        }
        p {
            text-align: center;
            font-size: calc(0.8rem + 0.1vw) !important;;
        }
    }
    .footer-links {
        h4 {
            text-align: center;
        }
        li {
            display: flex;
            justify-content: center;
        }
    }
    @include m.phone {
        .footer-about {
            align-items: start;
            .logo {
                margin-left: 0;
            }
            p {
                text-align: left;
            }
        }
        .footer-links {
            h4 {
                text-align: left;
            }
            li {
                justify-content: start;
            }
        }
    }
}

.features-2 .phone-mockup {
    margin-top: 0;
    @include m.desktop {
        margin-top: 3rem;
    }
}

.alert {
    font-size: calc(0.8rem + 0.2vw) !important;
    text-align: center;
}