@use "base/variables" as v;
@use "base/mixins" as m;

$default-font: "Roboto", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
$heading-font: "Nunito", sans-serif;
$nav-font: "Inter", sans-serif;

$background-color: #ffffff;
$default-color: #212529;
$heading-color: #2d465e;
$accent-color: #0d83fd;
$surface-color: #ffffff;
$contrast-color: #ffffff;
$light-background: #f3f9ff;

.blog-grid {
    padding: 0px 0;

    @include m.desktopXL {
        padding: 150px 0 200px 0;
    }
}

.blog-card {
    background: white;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.blog-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
}

.blog-card-header {
    height: 200px;
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
}

.blog-card-header.gradient-1 {
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-2 {
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-3 {
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-4 {
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-5 {
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-6 {
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-icon {
    font-size: 4rem;
    color: var(--accent-color);
    opacity: 0.9;
}

.blog-card-body {
    padding: 2rem;
    flex-grow: 1;
    display: flex;
    flex-direction: column;

    .blog-card-title {
        font-size: calc(1.1rem + 0.3vw) !important;
    }
}

.blog-card-title {
    font-size: calc(1.1rem + 0.3vw);
    color: #2c3e50;
    margin-bottom: 1rem;
    font-weight: 700;
}

.blog-card-excerpt {
    color: #666;
    font-size: calc(0.85rem + 0.1vw);
    margin-bottom: 1.5rem;
    line-height: 1.6;
    flex-grow: 1;
}

.blog-card-meta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.9rem;
    color: #999;
    margin-bottom: 1rem;
    padding-top: 1rem;
    border-top: 1px solid #eee;
}

.blog-card-meta i {
    margin-right: 0.5rem;
}

.blog-card-link {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--contrast-accent);
    font-size: calc(0.8rem + 0.3vw) !important;
    font-weight: 600;
    transition: all 0.3s ease;
}

.blog-card-link i {
    transition: transform 0.3s ease;
}

.blog-card-link:hover {
    color: color-mix(in srgb, var(--accent-color), black 20%);
}

.blog-card-link:hover i {
    transform: translateX(5px);
}

.section-intro {
    text-align: center;
    margin-bottom: 3rem;
}

.section-intro h2 {
    color: #2c3e50;
    margin-bottom: 1rem;
}

.section-intro p {
    color: #666;
    max-width: 700px;
    margin: 0 auto;
}

.blog-navigation {
    display: none;
    @include m.phone {
        display: flex;
    }
    margin-top: 2rem;

    @include m.desktopXL {
        margin-top: 5rem;
    }

    justify-content: space-between;
}

.blog-navigation-arrow {
    display: flex;
    @include m.phone {
        display: none;
    }
    .btn {
        background-color: transparent;
        padding: 0;
        margin: 2rem;
        i {
            color: var(--accent-color);
        }
    }
    justify-content: space-between;
}

.cta-banner {
    background: color-mix(in srgb, var(--accent-color), transparent 90%);
    color: white;
    padding: 4rem 2rem;
    border-radius: 12px;
    text-align: center;
    margin: 4rem 0;
}

.cta-banner h3 {
    font-size: calc(1.5rem + 0.5vw);
    color: var(--accent-color);
    font-weight: bold;
    margin-bottom: 1rem;
}

.cta-banner p {
    font-size: calc(1rem + 0.2vw);
    margin-bottom: 2rem;
    color: v.$black;
}


// ================================================================
// Blog Content Section
// ================================================================

.blog-content {
    padding: 0px 0;

    @include m.desktopXL {
        padding: 150px 0 200px 0;
    }

    .container {
        max-width: 1140px;
    }

    // Typography
    h2 {
        color: $heading-color;
        font-size: 2.25rem;
        font-weight: 700;
        margin-top: 3rem;
        margin-bottom: 1.5rem;
        padding-bottom: 1rem;
        position: relative;

        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 60px;
            height: 3px;
            background: $accent-color;
        }

        &:first-child {
            margin-top: 0;
        }
    }

    h3 {
        color: $heading-color;
        font-size: 1.75rem;
        font-weight: 600;
        margin-top: 2.5rem;
        margin-bottom: 1.25rem;
    }

    h4 {
        color: $heading-color;
        font-size: 1.4rem;
        font-weight: 600;
        margin-top: 2rem;
        margin-bottom: 1rem;
    }

    p {
        color: color-mix(in srgb, $default-color, transparent 15%);
        font-size: 1.1rem;
        line-height: 1.8;
        margin-bottom: 1.5rem;
        text-align: justify;
    }

    // Lists
    ul,
    ol {
        margin-bottom: 2rem;
        padding-left: 2rem;

        li {
            font-size: calc(0.85rem + 0.3vw) !important;
            color: color-mix(in srgb, $default-color, transparent 15%);
            line-height: 1.8;
            margin-bottom: 1rem;
            padding-left: 0.5rem;

            strong {
                color: $heading-color;
                font-weight: 600;
            }
        }
    }

    ul {
        list-style: none;
        padding-left: 0;

        li {
            position: relative;
            padding-left: 2rem;

            &::before {
                content: "";
                position: absolute;
                left: 0;
                top: 0.6em;
                width: 8px;
                height: 8px;
                background: $accent-color;
                border-radius: 50%;
            }
        }
    }

    // Highlight Box
    .highlight-box {
        background: linear-gradient(135deg,
                color-mix(in srgb, $accent-color, transparent 95%),
                color-mix(in srgb, $accent-color, transparent 92%));
        border-left: 4px solid $accent-color;
        border-radius: 10px;
        padding: 1.5rem;

        @include m.tablet {
            padding: 2rem;
        }

        margin: 2.5rem 0;
        font-size: calc(0.7rem + 0.5vw) !important;

        strong {
            color: $accent-color;
            font-size: calc(0.8rem + 0.5vw) !important;
            display: block;
            margin-bottom: 0.75rem;
            font-weight: 700;
        }

        p {
            margin-bottom: 0;
            color: $default-color;
        }
    }

    // CTA Section within content
    .cta-section {
        background: $accent-color;
        border-radius: 15px;
        padding: 3rem 2.5rem;
        margin: 3rem 0;
        text-align: center;
        position: relative;
        overflow: hidden;

        &::before {
            content: "";
            position: absolute;
            top: -50%;
            right: -10%;
            width: 300px;
            height: 300px;
            background: color-mix(in srgb, $contrast-color, transparent 90%);
            border-radius: 50%;
            z-index: 0;
        }

        &::after {
            content: "";
            position: absolute;
            bottom: -30%;
            left: -5%;
            width: 200px;
            height: 200px;
            background: color-mix(in srgb, $contrast-color, transparent 85%);
            border-radius: 50%;
            z-index: 0;
        }

        >* {
            position: relative;
            z-index: 1;
        }

        h3 {
            color: $contrast-color;
            font-size: 2rem;
            margin-top: 0;
            margin-bottom: 1rem;
        }

        p {
            color: $contrast-color;
            opacity: 0.95;
            font-size: 1.15rem;
            margin-bottom: 2rem;
            text-align: center;
        }

        .btn {
            background: $contrast-color;
            color: $accent-color;
            padding: 1rem 2.5rem;
            border-radius: 50px;
            font-weight: 600;
            font-size: 1.05rem;
            text-decoration: none;
            display: inline-block;

            &:hover {
                background: color-mix(in srgb, $contrast-color, transparent 5%);
                transform: translateY(-3px);
                box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
            }
        }
    }

    // Images
    img {
        max-width: 100%;
        height: auto;
        border-radius: 15px;
        margin: 2rem 0;
    }

    // Blockquotes
    blockquote {
        border-left: 4px solid $accent-color;
        padding-left: 2rem;
        margin: 2rem 0;
        font-style: italic;
        color: color-mix(in srgb, $default-color, transparent 25%);
        font-size: 1.15rem;

        p {
            margin-bottom: 0.5rem;
        }

        footer {
            font-size: 0.9rem;
            color: color-mix(in srgb, $default-color, transparent 40%);
            margin-top: 0.5rem;

            &::before {
                content: "— ";
            }
        }
    }

    // Code blocks
    code {
        background: color-mix(in srgb, $accent-color, transparent 95%);
        color: $accent-color;
        padding: 0.2rem 0.5rem;
        border-radius: 4px;
        font-size: 0.9em;
    }

    pre {
        background: color-mix(in srgb, $default-color, transparent 97%);
        padding: 1.5rem;
        border-radius: 10px;
        overflow-x: auto;
        margin: 2rem 0;

        code {
            background: none;
            color: $default-color;
            padding: 0;
        }
    }

    // Tables
    table {
        width: 100%;
        margin: 2rem 0;
        border-collapse: collapse;
        border-radius: 10px;
        overflow: hidden;

        thead {
            background: $accent-color;
            color: $contrast-color;

            th {
                padding: 1rem;
                text-align: left;
                font-weight: 600;
            }
        }

        tbody {
            tr {
                border-bottom: 1px solid color-mix(in srgb, $default-color, transparent 90%);

                &:hover {
                    background: color-mix(in srgb, $accent-color, transparent 97%);
                }

                &:last-child {
                    border-bottom: none;
                }
            }

            td {
                padding: 1rem;
            }
        }
    }
}