body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(1.15rem + 0.3vw);
}

h2 {
  font-size: calc(1.2rem + 0.8vw) !important;
}

h3 {
  font-size: calc(1.1rem + 0.6vw) !important;
}

h4 {
  font-size: calc(0.8rem + 0.3vw) !important;
}

a {
  text-decoration: none;
  color: #212121;
}

p {
  font-size: calc(0.85rem + 0.3vw) !important;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.logo {
  margin-left: auto;
}
@media (min-width: 1200px) {
  .logo {
    margin-left: 0;
  }
}

.btn-light {
  background: var(--contrast-color);
  color: var(--accent-color);
  padding: 0.75rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  margin-top: 1rem;
  display: inline-block;
  transition: all 0.3s ease;
}

.btn-light:hover {
  background: var(--accent-color);
  color: var(--contrast-color);
  transition: all 0.3s ease;
}

.hero {
  padding-top: 7rem;
}
@media (min-width: 992px) {
  .hero {
    padding-top: 10rem;
  }
}

.about .about-meta {
  width: 100%;
}
.about .about-meta,
.about .about-title,
.about .about-description {
  text-align: center;
}
.about .about-title {
  font-size: calc(1.5rem + 0.5vw);
}
.about .profile,
.about .contact-info {
  justify-content: center;
}
.about .about-img {
  display: none;
}
@media (min-width: 576px) {
  .about .about-meta,
.about .about-title,
.about .about-description {
    text-align: left;
  }
  .about .profile,
.about .contact-info {
    justify-content: start;
  }
}
@media (min-width: 1200px) {
  .about .about-img {
    display: block;
  }
}

.features .tab-content h3 {
  text-align: center;
}
.features h3 {
  font-size: calc(1.1rem + 0.6vw) !important;
}
.features h3:after {
  width: 90% !important;
  right: 0;
  margin: auto;
}
.features span {
  font-size: calc(0.8rem + 0.3vw) !important;
}
@media (min-width: 576px) {
  .features .tab-content h3 {
    text-align: left;
  }
  .features h3:after {
    width: 75% !important;
    right: unset;
  }
}

.faq-9 h3 {
  font-size: calc(0.8rem + 0.3vw) !important;
}
.faq-9 .faq-title, .faq-9 .faq-description {
  text-align: center;
}
@media (min-width: 576px) {
  .faq-9 .faq-title, .faq-9 .faq-description {
    text-align: left;
  }
}

.footer .footer-about {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer .footer-about .logo {
  margin: auto;
  margin-top: 0;
  margin-bottom: 0;
}
.footer .footer-about p {
  text-align: center;
  font-size: calc(0.8rem + 0.1vw) !important;
}
.footer .footer-links h4 {
  text-align: center;
}
.footer .footer-links li {
  display: flex;
  justify-content: center;
}
@media (min-width: 576px) {
  .footer .footer-about {
    align-items: start;
  }
  .footer .footer-about .logo {
    margin-left: 0;
  }
  .footer .footer-about p {
    text-align: left;
  }
  .footer .footer-links h4 {
    text-align: left;
  }
  .footer .footer-links li {
    justify-content: start;
  }
}

.features-2 .phone-mockup {
  margin-top: 0;
}
@media (min-width: 992px) {
  .features-2 .phone-mockup {
    margin-top: 3rem;
  }
}

.alert {
  font-size: calc(0.8rem + 0.2vw) !important;
  text-align: center;
}

.blog-grid {
  padding: 0px 0;
}
@media (min-width: 1200px) {
  .blog-grid {
    padding: 150px 0 200px 0;
  }
}

.blog-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.blog-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
}

.blog-card-header {
  height: 200px;
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.blog-card-header.gradient-1 {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-2 {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-3 {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-4 {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-5 {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-header.gradient-6 {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
}

.blog-card-icon {
  font-size: 4rem;
  color: var(--accent-color);
  opacity: 0.9;
}

.blog-card-body {
  padding: 2rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.blog-card-body .blog-card-title {
  font-size: calc(1.1rem + 0.3vw) !important;
}

.blog-card-title {
  font-size: calc(1.1rem + 0.3vw);
  color: #2c3e50;
  margin-bottom: 1rem;
  font-weight: 700;
}

.blog-card-excerpt {
  color: #666;
  font-size: calc(0.85rem + 0.1vw);
  margin-bottom: 1.5rem;
  line-height: 1.6;
  flex-grow: 1;
}

.blog-card-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
  color: #999;
  margin-bottom: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #eee;
}

.blog-card-meta i {
  margin-right: 0.5rem;
}

.blog-card-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--contrast-accent);
  font-size: calc(0.8rem + 0.3vw) !important;
  font-weight: 600;
  transition: all 0.3s ease;
}

.blog-card-link i {
  transition: transform 0.3s ease;
}

.blog-card-link:hover {
  color: color-mix(in srgb, var(--accent-color), black 20%);
}

.blog-card-link:hover i {
  transform: translateX(5px);
}

.section-intro {
  text-align: center;
  margin-bottom: 3rem;
}

.section-intro h2 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.section-intro p {
  color: #666;
  max-width: 700px;
  margin: 0 auto;
}

.blog-navigation {
  display: none;
  margin-top: 2rem;
  justify-content: space-between;
}
@media (min-width: 576px) {
  .blog-navigation {
    display: flex;
  }
}
@media (min-width: 1200px) {
  .blog-navigation {
    margin-top: 5rem;
  }
}

.blog-navigation-arrow {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 576px) {
  .blog-navigation-arrow {
    display: none;
  }
}
.blog-navigation-arrow .btn {
  background-color: transparent;
  padding: 0;
  margin: 2rem;
}
.blog-navigation-arrow .btn i {
  color: var(--accent-color);
}

.cta-banner {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
  color: white;
  padding: 4rem 2rem;
  border-radius: 12px;
  text-align: center;
  margin: 4rem 0;
}

.cta-banner h3 {
  font-size: calc(1.5rem + 0.5vw);
  color: var(--accent-color);
  font-weight: bold;
  margin-bottom: 1rem;
}

.cta-banner p {
  font-size: calc(1rem + 0.2vw);
  margin-bottom: 2rem;
  color: #212121;
}

.blog-content {
  padding: 0px 0;
}
@media (min-width: 1200px) {
  .blog-content {
    padding: 150px 0 200px 0;
  }
}
.blog-content .container {
  max-width: 1140px;
}
.blog-content h2 {
  color: #2d465e;
  font-size: 2.25rem;
  font-weight: 700;
  margin-top: 3rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  position: relative;
}
.blog-content h2::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background: #0d83fd;
}
.blog-content h2:first-child {
  margin-top: 0;
}
.blog-content h3 {
  color: #2d465e;
  font-size: 1.75rem;
  font-weight: 600;
  margin-top: 2.5rem;
  margin-bottom: 1.25rem;
}
.blog-content h4 {
  color: #2d465e;
  font-size: 1.4rem;
  font-weight: 600;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.blog-content p {
  color: color-mix(in srgb, #212529, transparent 15%);
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 1.5rem;
  text-align: justify;
}
.blog-content ul,
.blog-content ol {
  margin-bottom: 2rem;
  padding-left: 2rem;
}
.blog-content ul li,
.blog-content ol li {
  font-size: calc(0.85rem + 0.3vw) !important;
  color: color-mix(in srgb, #212529, transparent 15%);
  line-height: 1.8;
  margin-bottom: 1rem;
  padding-left: 0.5rem;
}
.blog-content ul li strong,
.blog-content ol li strong {
  color: #2d465e;
  font-weight: 600;
}
.blog-content ul {
  list-style: none;
  padding-left: 0;
}
.blog-content ul li {
  position: relative;
  padding-left: 2rem;
}
.blog-content ul li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.6em;
  width: 8px;
  height: 8px;
  background: #0d83fd;
  border-radius: 50%;
}
.blog-content .highlight-box {
  background: linear-gradient(135deg, color-mix(in srgb, #0d83fd, transparent 95%), color-mix(in srgb, #0d83fd, transparent 92%));
  border-left: 4px solid #0d83fd;
  border-radius: 10px;
  padding: 1.5rem;
  margin: 2.5rem 0;
  font-size: calc(0.7rem + 0.5vw) !important;
}
@media (min-width: 768px) {
  .blog-content .highlight-box {
    padding: 2rem;
  }
}
.blog-content .highlight-box strong {
  color: #0d83fd;
  font-size: calc(0.8rem + 0.5vw) !important;
  display: block;
  margin-bottom: 0.75rem;
  font-weight: 700;
}
.blog-content .highlight-box p {
  margin-bottom: 0;
  color: #212529;
}
.blog-content .cta-section {
  background: #0d83fd;
  border-radius: 15px;
  padding: 3rem 2.5rem;
  margin: 3rem 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}
.blog-content .cta-section::before {
  content: "";
  position: absolute;
  top: -50%;
  right: -10%;
  width: 300px;
  height: 300px;
  background: color-mix(in srgb, #ffffff, transparent 90%);
  border-radius: 50%;
  z-index: 0;
}
.blog-content .cta-section::after {
  content: "";
  position: absolute;
  bottom: -30%;
  left: -5%;
  width: 200px;
  height: 200px;
  background: color-mix(in srgb, #ffffff, transparent 85%);
  border-radius: 50%;
  z-index: 0;
}
.blog-content .cta-section > * {
  position: relative;
  z-index: 1;
}
.blog-content .cta-section h3 {
  color: #ffffff;
  font-size: 2rem;
  margin-top: 0;
  margin-bottom: 1rem;
}
.blog-content .cta-section p {
  color: #ffffff;
  opacity: 0.95;
  font-size: 1.15rem;
  margin-bottom: 2rem;
  text-align: center;
}
.blog-content .cta-section .btn {
  background: #ffffff;
  color: #0d83fd;
  padding: 1rem 2.5rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1.05rem;
  text-decoration: none;
  display: inline-block;
}
.blog-content .cta-section .btn:hover {
  background: color-mix(in srgb, #ffffff, transparent 5%);
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}
.blog-content img {
  max-width: 100%;
  height: auto;
  border-radius: 15px;
  margin: 2rem 0;
}
.blog-content blockquote {
  border-left: 4px solid #0d83fd;
  padding-left: 2rem;
  margin: 2rem 0;
  font-style: italic;
  color: color-mix(in srgb, #212529, transparent 25%);
  font-size: 1.15rem;
}
.blog-content blockquote p {
  margin-bottom: 0.5rem;
}
.blog-content blockquote footer {
  font-size: 0.9rem;
  color: color-mix(in srgb, #212529, transparent 40%);
  margin-top: 0.5rem;
}
.blog-content blockquote footer::before {
  content: "— ";
}
.blog-content code {
  background: color-mix(in srgb, #0d83fd, transparent 95%);
  color: #0d83fd;
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  font-size: 0.9em;
}
.blog-content pre {
  background: color-mix(in srgb, #212529, transparent 97%);
  padding: 1.5rem;
  border-radius: 10px;
  overflow-x: auto;
  margin: 2rem 0;
}
.blog-content pre code {
  background: none;
  color: #212529;
  padding: 0;
}
.blog-content table {
  width: 100%;
  margin: 2rem 0;
  border-collapse: collapse;
  border-radius: 10px;
  overflow: hidden;
}
.blog-content table thead {
  background: #0d83fd;
  color: #ffffff;
}
.blog-content table thead th {
  padding: 1rem;
  text-align: left;
  font-weight: 600;
}
.blog-content table tbody tr {
  border-bottom: 1px solid color-mix(in srgb, #212529, transparent 90%);
}
.blog-content table tbody tr:hover {
  background: color-mix(in srgb, #0d83fd, transparent 97%);
}
.blog-content table tbody tr:last-child {
  border-bottom: none;
}
.blog-content table tbody td {
  padding: 1rem;
}

/* Portfolio Hero */
.portfolio-hero {
  padding-top: 160px;
  padding-bottom: 80px;
  background: linear-gradient(135deg, color-mix(in srgb, var(--accent-color), transparent 95%) 50%, color-mix(in srgb, var(--accent-color), transparent 98%) 25%, transparent 50%);
  position: relative;
}

.portfolio-hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: radial-gradient(circle at 90% 10%, color-mix(in srgb, var(--accent-color), transparent 92%), transparent 40%); */
  pointer-events: none;
}

.portfolio-hero h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.portfolio-hero p {
  font-size: 1.2rem;
  color: color-mix(in srgb, var(--default-color), transparent 20%);
}

/* Portfolio Grid */
.portfolio {
  padding: 50px 0;
}
@media (min-width: 1200px) {
  .portfolio {
    padding: 150px 0 200px 0;
  }
}

.portfolio-item {
  background: var(--surface-color);
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.portfolio-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
}

.portfolio-image {
  position: relative;
  overflow: hidden;
  height: 250px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.portfolio-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.portfolio-item:hover .portfolio-image img {
  transform: scale(1.1);
}

.portfolio-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.7) 100%);
  display: flex;
  align-items: flex-end;
  padding: 1.5rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.portfolio-item:hover .portfolio-overlay {
  opacity: 1;
}

.portfolio-category {
  background: var(--accent-color);
  color: var(--contrast-color);
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
}

.portfolio-content {
  padding: 2rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.portfolio-content h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.portfolio-content p {
  color: color-mix(in srgb, var(--default-color), transparent 30%);
  margin-bottom: 1.5rem;
  flex-grow: 1;
}

.portfolio-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.portfolio-tag {
  background: color-mix(in srgb, var(--accent-color), transparent 90%);
  color: var(--contrast-accent);
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: 500;
}

.portfolio-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--contrast-accent);
  font-size: calc(0.8rem + 0.3vw) !important;
  font-weight: 600;
  transition: all 0.3s ease;
}

.portfolio-link i {
  transition: transform 0.3s ease;
}

.portfolio-link:hover {
  color: color-mix(in srgb, var(--accent-color), black 20%);
}

.portfolio-link:hover i {
  transform: translateX(5px);
}

/* Responsive */
@media (max-width: 992px) {
  .portfolio-hero {
    padding-top: 120px;
    padding-bottom: 60px;
  }
}